.club{
    padding: 100px 0 40px;
    position: relative;
    overflow: hidden;
}

.club:before {
    content: '';
    background: url(http://localhost:3000/static/media/2-image.2f69bb6573a6b4f3cd38.png);
    position: absolute;
    height: 100%;
    width: 100%;
    top: -220px;
    left: 0;
    transform: rotate(-18deg);
    opacity: 1;
    background-size: contain;
    background-repeat: no-repeat;
}

.club .left_side{
    position: relative;
    padding: 0px 65px;
}

.club .left_side:before {
    content: '';
    position: absolute;
    top: -87px;
    right: 8px;
    height: 80px;
    width: 80px;
    background: #ad9956;
    border-radius: 10px;
}

.club .left_side .tag {
    position: absolute;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #9f9f9f;
    transform: rotate(-90deg);
    top: 60px;
    left: 60px;
}

.club .left_side .tag:before {
    content: '';
    position: absolute;
    height: 100px;
    width: 1px;
    background: #ccc;
    transform: rotate(90deg);
    left: -64px;
    top: -42px;
}


.club .left_content{
    position: absolute;
    padding: 0 150px;
    top: 0;
    left: 0;
}

.club h1.heading {
    font-size: 30px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    transition: all 0.5s 1s;
    color: #000;
    font-family: 'Cinzel';
    font-weight: 400;
    transform: translateX(-60px);
    opacity: 0;
    transition: 0.5s;
}

.club .left_content.active h1.heading{
    transform: translateX(0px);
    transition-delay: 0.6s;
    opacity: 1;
}

.club p.content {
    font-size: 14px;
    width: 74%;
    transform: translateX(-60px);
    opacity: 0;
    transition: 0.5s;
}

.club .left_content.active p.content{
    transform: translateX(0px);
    transition-delay: 0.6s;
    opacity: 1;
}

.club .left_side .btn-outline{
    position: relative;
    transition: 0.5s;
    border-radius: 51px;
    font-size: 12px;
    color: #000;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: 1px solid #979797;
    padding: 12px 20px;
    font-weight: 500;
    margin-top: 20px;
    transform: translateX(-60px);
    opacity: 0;
    transition: 0.5s;
}

.club .left_side .left_content.active .btn-outline{
    transform: translateX(0px);
    transition-delay: 0.6s;
    opacity: 1;
}


.club .title {
    text-align: center;
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 30px;
}

.club .clubSlider {
    transition: 0.5s all linear;
    will-change: transform;
    height: 350px;
    overflow: hidden;
}

.club .slider {
    width: 100%;
    overflow: hidden;
    position: relative;
}

.club .slider .wrapper {
    transition: 0.5s all linear;
    will-change: transform;
    height: 350px;
}

.club .slider .each-slide {
    width: 100vw;
    height: 100%;
    float: left;
    text-align: center;
    background-size: cover;
    background-position: center center;
    background-color: transparent;
}

.club .slider .each-slide img{
    width: 40vw;
    height: 100%;
    border-radius: 10px;
    margin-right: 60vw;
}

.arrows{
    text-align: center;
}

button.arrows.prev,
button.arrows.next {
    position: relative;
    border: 0;
    padding: 0;
    height: 30px;
    width: 30px;
    background: #ad9956;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    margin: 0 20px;
    margin-top: 20px;
}

.club .responsive_arrow{
    display: none;
}

@media only screen and (max-width: 768px) {
    .club .slider .each-slide img{
        width: 100vw;
        border-radius: 0;
        margin-right: 0;
    }

    .club .desktop_arrow{
        display: none;
    }

    .club .responsive_arrow{
        display: block;
    }

    .club .title{
        margin-top: 0;
    }

    .club .slider .wrapper{
        height: 300px;
    }

    .club .left_side .tag {
        transform: rotate(0deg);
        top: -57px;
        left: 90px;
    }

    .club .left_side{
        padding: 0 20px;
    }

    .club .left_content {
        padding: 0;
        text-align: center;
    }

    .club p.content{
        width: 100%;
    }

    .club .clubSlider{
        height: 370px;
    }

    .club .left_side:before{
        transform: rotate(45deg);
        right: -40px;
        top: -87px;
    }
}