.home .section5 {
    background: url('../../../public/image/new-1.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 150px 0;
    position: relative;
    box-shadow: inset -34px 77px 121px 110px #000000bf;
}

.home .section5 .container{
    position: relative;
}

.home .section5 .content {
    display: flex;
    justify-content: space-between;
}

.home .section5 h4{
    font-family: 'Parisienne';
    font-size: 50px;
    color: #ad9956;
    margin-bottom: 0;
    line-height: 38px;
    position: relative;
}

.home .section5 p{
    font-size: 13px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 18px;
}

.home .section5.divider {
    height: 20px;
    width: 3px;
    background: #b9b9b9;
    position: relative;
    top: 30px;
}

@media only screen and (max-width: 768px) {
    .home .section5 .content {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .home .section5 .content > div {
        margin-bottom: 20px;
    }

    .home .section5 .content > div:nth-last-child(1){
        margin-bottom: 0;
    }

    .home .section5 {
        background-attachment: fixed;
        padding: 60px 0;
    }
}