.newLaunch {
    padding: 50px 0;
    position: relative;
    overflow: hidden;
}

.newLaunch::before{
    content: '';
    background: url('../../../public/image/2-image.png');
    position: absolute;
    height: 100%;
    width: 100%;
    top: -220px;
    left: -364px;
    transform: rotate(-85deg);
    opacity: 1;
    background-size: contain;
}

.newLaunch .container{
    position: relative;
}

.newLaunch h3.heading {
    margin-bottom: 20px;
    font-size: 20px;
    letter-spacing: 4px;
    color: #000;
}


.newLaunch p.heading{
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    transition: all 0.5s 1s;
    color: #000;
    font-family: 'Cinzel';
    font-weight: 300;
    position: relative;
    line-height: 20px;
    padding-left: 20px;    
    padding-top: 40px;
    transform: translateY(-30px);
    opacity: 0;
    margin-bottom: 10px;
}


.newLaunch .slider.s--ready p.heading {
    transform: translateY(0);
    opacity: 1;
}

.newLaunch p.heading::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 60px;
    width: 6px;
    background: #ad9956;
}

.newLaunch .btn-outline{
    position: relative;
    backface-visibility: hidden;
    transition: 0.5s;
    border-radius: 51px;
    font-size: 12px;
    color: #000;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: 1px solid #979797;
    width: fit-content;
    padding: 12px 20px;
    font-weight: 500;
    margin-top: 40px;
}

.newLaunch p{
    font-size: 14px;
    letter-spacing: 2px;
    font-weight: 400;
    margin-bottom: 4px;
    text-transform: initial;
    color: #000;
}

.newLaunch .divider {
    height: 1.7px;
    width: 20px;
    background: #000;
    margin: 10px 0;
}

.newLaunch .content{
    height: 100%;
    display: flex;
    align-items: end;
    justify-content: end;
    padding-right: 50px;
}

.newLaunch .slider {
    overflow: hidden;
    position: relative;
    height: 640px;
}

.newLaunch .slider__slides {
    position: relative;
    height: 100%;
}

.newLaunch .slider__slide {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.newLaunch .slider__slide.s--active {
    pointer-events: auto;
}

.newLaunch .slider__slide-subheading {
    margin-bottom: 20px;
    font-size: 20px;
    letter-spacing: 4px;
    transform: translateY(20px);
    opacity: 0;
    transition: 0.5s;
    color: #000;
}

.newLaunch .slider__slide.s--active .slider__slide-subheading {
    transition-delay: 0.65s;
    opacity: 1;
    transform: translateY(0);
}

.newLaunch .slider__slide-heading {
    display: flex;
    margin-bottom: 20px;
    font-size: 60px;
    letter-spacing: 12px;
    opacity: 0;
    transition: 0.5s;
    color: #000;    
}

.newLaunch .slider__slide-heading p {
    font-size: 14px;
    letter-spacing: 2px;
    font-weight: 400;
    margin-bottom: 4px;
    text-transform: initial;
    line-height: 20px;
}

.newLaunch .slider__slide-heading p b{
    text-transform: uppercase;
}

.newLaunch .slider__slide.s--active .slider__slide-heading{
    transition-delay: 0.65s;
    opacity: 1;
}

.newLaunch .slider__slide-heading span {
    display: block;
    opacity: 0;
    transform: translateY(-60px);
    transition: all 0.3333333333s;
}

.newLaunch .slider_cetner-image {
    opacity: 0;
    transition: 1s;
    animation-name: zoom;
    animation-duration: 1s;
}

@keyframes zoom {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

.newLaunch .slider__slide.s--active .slider_cetner-image {
    opacity: 1;
}

.newLaunch .slider__slide.s--prev .slider__slide-heading span {
    transform: translateY(60px);
}

.newLaunch .slider__slide.s--active .slider__slide-heading span {
    opacity: 1;
    transform: translateY(0);
}

.newLaunch .slider__slide-heading span:nth-child(1) {
    transition-delay: 0s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(1) {
    transition-delay: 0.3333333333s;
}

.newLaunch .slider__slide-heading span:nth-child(2) {
    transition-delay: 0.1s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(2) {
    transition-delay: 0.4333333333s;
}

.newLaunch .slider__slide-heading span:nth-child(3) {
    transition-delay: 0.2s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(3) {
    transition-delay: 0.5333333333s;
}

.newLaunch .slider__slide-heading span:nth-child(4) {
    transition-delay: 0.3s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(4) {
    transition-delay: 0.6333333333s;
}

.newLaunch .slider__slide-heading span:nth-child(5) {
    transition-delay: 0.4s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(5) {
    transition-delay: 0.7333333333s;
}

.newLaunch .slider__slide-heading span:nth-child(6) {
    transition-delay: 0.5s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(6) {
    transition-delay: 0.8333333333s;
}

.newLaunch .slider__slide-heading span:nth-child(n+7) {
    transition-delay: 0.6s;
}

.newLaunch .slider__slide.s--active .slider__slide-heading span:nth-child(n+7) {
    transition-delay: 0.9333333333s;
}

.newLaunch .slider__slide-readmore {
    position: relative;
    backface-visibility: hidden;
    transform: translateY(-20px);
    cursor: pointer;
    opacity: 0;
    transition: 0.5s;
    border-radius: 51px;
    font-size: 12px;
    color: #000;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: 1px solid #979797;
    width: fit-content;
    padding: 12px 20px;
    font-weight: 500;
    margin-top: 40px;
}

.newLaunch .slider__slide.s--active .slider__slide-readmore {
    transition-delay: 0.65s;
    opacity: 1;
    transform: translateY(0);
}

.newLaunch .slider__control {
    z-index: 100;
    position: absolute;
    left: 0px;
    top: 50%;
    width: 50px;
    height: 50px;
    margin-top: -25px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0);
    transform: translateX(-50px);
    opacity: 0;
    transition: all 0.5s 1s;
    cursor: pointer;
}

.newLaunch .slider__control:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 24px;
    height: 24px;
    margin-left: -10px;
    margin-top: -10px;
    border: 1px solid #8e8e8e;
    border-bottom: none;
    border-right: none;
    transform: translateX(5px) rotate(-45deg);
}

.newLaunch .slider__control:hover:before{
    border-color: #000;
}

.newLaunch .slider__control--left{
    opacity: 0;
}

.newLaunch .slider__control--right {
    left: auto;
    right: 50px;
    transform: translateX(50px);
    display: none;
}

.newLaunch .slider__control--right:before {
    transform: translateX(-5px) rotate(135deg);
}

.newLaunch .slider.s--ready .slider__control {
    transform: translateX(0);
    opacity: 1;
}

@media only screen and (max-width: 768px) {
    .newLaunch .container{
        padding: 0;
    }

    .newLaunch .slider.s--ready .slider__control {
        top: auto;
        bottom: 35%;
    }

    .newLaunch .slider__control--right{
        display: block;
        right: 0;
    }

    .newLaunch .content {
        text-align: center;
        width: 100%;
        padding: 0;
        justify-content: center;
    }

    .newLaunch .divider {
        height: 1.8px;
        margin: 10px auto;
    }

    .newLaunch p.heading {
        text-align: center;
        width: fit-content;
        margin: auto;
        margin-bottom: 60px;
        padding-top: 0;
    }

    .newLaunch p.heading::before {
        height: 6px;
        width: 60px;
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%);
    }

    .newLaunch .slider__slide-heading {
        margin-bottom: 30px;
    }

    .newLaunch .slider__slide-readmore {
        margin-left: auto;
        margin-right: auto;
    }
}
