.contact {
    padding-top: 100px;
}

.contact form{
    margin-top: 50px;
}

.contact .form-control {
    border-radius: 51px;
    font-size: 13px;
    color: #000;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 24px;
    height: 41px;
}

.contact .form-control::placeholder{
    color: #000;
}

.contact .form-control[type="submit"]{
    border-color: #979797;
    margin-top: 20px;
    padding: 0 30px;
}

.contact h3{
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 10px;
    width: fit-content;
    margin: auto;
    position: relative;
}

.contact h3::before{
    content: '';
    position: absolute;
    left: 50%;
    top: -20px;
    transform: translateX(-50%);
    width: 74%;
    height: 1px;
    background: #ccc;
}

@media only screen and (max-width: 768px) {
    
    .contact form{
        padding: 0 50px;
    }

    .contact .form-control[type="submit"] {
        width: fit-content;
        margin-left: auto;
        margin-right: auto;
    }
}