.projects {
    padding-bottom: 50px;
}

.projects .slider {
    overflow: hidden;
    position: relative;
    height: 550px;
    color: #fff;
}

.projects .slider__top-heading {
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    transition: all 0.5s 1s;
    transform: translateY(-30px);
    opacity: 0;
    color: #000;
    font-family: 'Cinzel';
    font-weight: 300;
    position: relative;
    padding-left: 20px;
    position: absolute;
    left: 48px;
    top: 0;
    z-index: 2;
    margin-top: 40px;
    line-height: 20px;
}

.projects .slider__top-heading::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 60px;
    width: 6px;
    background: #ad9956;
}

.projects .slider.s--ready .slider__top-heading {
    transform: translateY(0);
    opacity: 1;
}

.projects .slider__slides {
    position: relative;
    height: 100%;
}

.projects .slider__slides::before{
    content: '';
    position: absolute;
    height: 100%;
    width: 20px;
    top: 0;
    right: 0;
    background: #ad9956;
}

.projects .slider__slide {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.projects .slider__slide.s--active {
    pointer-events: auto;
}

.projects .slider__slide-content {
    z-index: 6;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    text-transform: uppercase;
    line-height: 1;
    padding: 60px 225px 0 50px;
}

.projects .slider__slide-subheading {
    margin-bottom: 20px;
    font-size: 20px;
    letter-spacing: 4px;
    transform: translateY(20px);
    opacity: 0;
    transition: 0.5s;
    color: #000;
}

.projects .slider__slide.s--active .slider__slide-subheading {
    transition-delay: 0.65s;
    opacity: 1;
    transform: translateY(0);
}

.projects .slider__slide-heading {
    display: flex;
    margin-bottom: 20px;
    font-size: 60px;
    letter-spacing: 12px;
    opacity: 0;
    transition: 0.5s;
    color: #000;    
}

.projects .slider__slide-heading p {
    font-size: 14px;
    letter-spacing: 2px;
    font-weight: 400;
    margin-bottom: 4px;
    text-transform: initial;
    line-height: 20px;
}

.projects .slider__slide-heading p b{
    text-transform: uppercase;
}

.projects .slider__slide-heading .divider{
    height: 1px;
    width: 30px;
    background: #000;
    margin: 20px 0;
}

.projects .slider__slide.s--active .slider__slide-heading{
    transition-delay: 0.65s;
    opacity: 1;
}

.projects .slider__slide-heading span {
    display: block;
    opacity: 0;
    transform: translateY(-60px);
    transition: all 0.3333333333s;
}

.projects .slider__slide.s--prev .slider__slide-heading span {
    transform: translateY(60px);
}

.projects .slider__slide.s--active .slider__slide-heading span {
    opacity: 1;
    transform: translateY(0);
}

.projects .slider__slide-heading span:nth-child(1) {
    transition-delay: 0s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(1) {
    transition-delay: 0.3333333333s;
}

.projects .slider__slide-heading span:nth-child(2) {
    transition-delay: 0.1s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(2) {
    transition-delay: 0.4333333333s;
}

.projects .slider__slide-heading span:nth-child(3) {
    transition-delay: 0.2s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(3) {
    transition-delay: 0.5333333333s;
}

.projects .slider__slide-heading span:nth-child(4) {
    transition-delay: 0.3s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(4) {
    transition-delay: 0.6333333333s;
}

.projects .slider__slide-heading span:nth-child(5) {
    transition-delay: 0.4s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(5) {
    transition-delay: 0.7333333333s;
}

.projects .slider__slide-heading span:nth-child(6) {
    transition-delay: 0.5s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(6) {
    transition-delay: 0.8333333333s;
}

.projects .slider__slide-heading span:nth-child(n+7) {
    transition-delay: 0.6s;
}

.projects .slider__slide.s--active .slider__slide-heading span:nth-child(n+7) {
    transition-delay: 0.9333333333s;
}

.projects .slider__slide-readmore {
    position: relative;
    backface-visibility: hidden;
    transform: translateY(-20px);
    cursor: pointer;
    opacity: 0;
    transition: 0.5s;
    border-radius: 51px;
    font-size: 12px;
    color: #000;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: 1px solid #979797;
    width: fit-content;
    padding: 12px 20px;
    font-weight: 500;
    margin-top: 20px;
}

.projects .slider__slide.s--active .slider__slide-readmore {
    transition-delay: 0.65s;
    opacity: 1;
    transform: translateY(0);
}

.projects .slider__slide-parts {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    width: 100%;
    height: 100%;
}

.projects .slider__slide-parts:after {
    content: "";
    z-index: 5;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
}

.projects .slider__slide-part {
    position: relative;
    width: 25%;
    height: 100%;
}

.projects .slider__slide-part-inner {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 100%;
    background-size: 0 0;
    background-repeat: no-repeat;
    transition: transform 0.5s ease-in-out;
}

.projects .slider__slide-part-inner:before {
    content: "";
    position: absolute;
    width: 100vw;
    height: 100%;
    background-image: inherit;
    background-size: cover;
    background-position: center center;
    transition: opacity 0.25s;
    opacity: 0;
}

.projects .slider__slide-part:nth-child(1) .slider__slide-part-inner {
    z-index: 3;
    transition-delay: 0.24s;
    transform: translateX(-32.5%);
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(1) .slider__slide-part-inner {
    transition-delay: 0.28s;
}

.projects .slider__slide-part:nth-child(1) .slider__slide-part-inner:before {
    left: 0vw;
    transition-delay: 0.365s;
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(1) .slider__slide-part-inner:before {
    transition-delay: 0.28s;
}

.projects .slider__slide-part:nth-child(2) .slider__slide-part-inner {
    z-index: 2;
    transition-delay: 0.16s;
    transform: translateX(-65%);
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(2) .slider__slide-part-inner {
    transition-delay: 0.36s;
}

.projects .slider__slide-part:nth-child(2) .slider__slide-part-inner:before {
    left: -14.79vw;
    transition-delay: 0.285s;
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(2) .slider__slide-part-inner:before {
    transition-delay: 0.36s;
}

.projects .slider__slide-part:nth-child(3) .slider__slide-part-inner {
    z-index: 1;
    transition-delay: 0.08s;
    transform: translateX(-97.5%);
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(3) .slider__slide-part-inner {
    transition-delay: 0.44s;
}

.projects .slider__slide-part:nth-child(3) .slider__slide-part-inner:before {
    left: -29.5vw;
    transition-delay: 0.205s;
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(3) .slider__slide-part-inner:before {
    transition-delay: 0.44s;
}

.projects .slider__slide-part:nth-child(4) .slider__slide-part-inner {
    z-index: 0;
    transition-delay: 0s;
    transform: translateX(-130%);
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(4) .slider__slide-part-inner {
    transition-delay: 0.52s;
}

.projects .slider__slide-part:nth-child(4) .slider__slide-part-inner:before {
    left: -44.2vw;
    transition-delay: 0.125s;
}

.projects .slider__slide.s--active .slider__slide-part:nth-child(4) .slider__slide-part-inner:before {
    transition-delay: 0.52s;
}

.projects .slider__slide.s--active .slider__slide-part-inner {
    transform: translateX(0);
    transition-timing-function: ease;
}

.projects .slider__slide.s--active .slider__slide-part-inner:before {
    opacity: 1;
}

.projects .slider__control {
    z-index: 100;
    position: absolute;
    left: 50px;
    top: 50%;
    width: 50px;
    height: 50px;
    margin-top: -25px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0);
    transform: translateX(-50px);
    opacity: 0;
    transition: all 0.5s 1s;
    cursor: pointer;
}

.projects .slider__control:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 24px;
    height: 24px;
    margin-left: -10px;
    margin-top: -10px;
    border: 1px solid #8e8e8e;
    border-bottom: none;
    border-right: none;
    transform: translateX(5px) rotate(-45deg);
}

.projects .slider__control:hover:before{
    border-color: #000;
}

.projects .slider__control--left{
    opacity: 0;
    display: none;
}

.projects .slider__control--right {
    left: auto;
    right: 50px;
    transform: translateX(50px);
}

.projects .slider__control--right:before {
    transform: translateX(-5px) rotate(135deg);
}

.projects .slider.s--ready .slider__control {
    transform: translateX(0);
    opacity: 1;
}

.projects .btn-outline{
    position: relative;
    backface-visibility: hidden;
    transition: 0.5s;
    border-radius: 10px;
    font-size: 12px;
    color: #000;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: 1px solid #979797;
    width: fit-content;
    padding: 12px 14px;
    font-weight: 500;
    margin-top: 50px;
}

@media only screen and (max-width: 768px) {
    .projects .slider__img {
        height: 300px;
    }

    .projects .slider {
        height: 810px;
    }

    .projects .slider__slide {
        position: static;
    }

    .projects .slider__slide-parts{
        position: relative;
    }

    .projects .slider__slides::before {
        opacity: 0;
    }

    .projects .slider.s--ready .slider__control {
        bottom: 112px;
        top: auto;
        background: #00000059;
    }

    .projects .slider__control:before{
        border-color: #fff;
        width: 16px;
        height: 16px;
        margin-left: -7px;
        margin-top: -7px;
    }

    .projects .slider__control--right {
        right: 10px;
    }

    .projects .slider__control--left{
        display: block;
        left: 10px;
    }

    .projects .slider__slide-content {
        text-align: center;
        padding: 0 20px;
    }

    .projects .slider__slide-heading .divider {
        margin-left: auto;
        margin-right: auto;
    }

    .projects .slider__slide-readmore {
        margin: 10px auto 40px;
    }

    .projects .slider__top-heading {
        width: 100%;
        text-align: center;
        padding: 0;
        margin: 0;
        left: 0;
        position: relative;
        margin-bottom: 40px;
    }

    .projects .slider__top-heading::before {
        height: 6px;
        width: 60px;
        left: 50%;
        bottom: -15px;
        transform: translateX(-50%);
    }

    .projects .btn-outline{
        margin-top: 30px;
    }
}