header .dropdown-toggle::after{
    display: none;
}

header .navbar-brand img{
    width: 150px;
}

header  nav.navbar.navbar-expand-lg {
    padding: 20px 0;
}

header .navbar-nav .dropdown-menu{
    background: #fff;
    border: 0;
    box-shadow: 2px 2px 10px #0000001f;
    border-radius: 10px;
}

@media only screen and (max-width: 768px) {
    header nav.navbar.navbar-expand-lg > div {
        flex-wrap: initial;
    }

    .navbar-toggler {
        display: none;
    }

    .collapse:not(.show) {
        display: block;
        text-align: right;
    }

    header .navbar-nav .dropdown-menu {
        position: absolute;
    }
}

