footer .footer_logo{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

footer .footer_logo img{
    width: 70px;
}

footer h1.heading {
    font-size: 16px;
    text-transform: uppercase;
    margin-bottom: 14px;
}

footer .content {
    font-size: 14px;
    position: relative;
}

footer p.content::before{
    content: '';
    position: absolute;
    left: 2px;
    bottom: -10px;
    width: 14px;
    height: 1px;
    background: #000;
    opacity: 0;
}

footer p.line::before{
    opacity: 1;
}

footer .f_b {
    border-top: 1px solid #ccc;
    padding: 50px 0;
    border-bottom: 1px solid #ccc;
    margin: 50px 0;
}

@media only screen and (max-width: 768px) {
    footer {
        text-align: center;
    }

    footer h1.heading {
        margin-top: 30px;
        margin-bottom: 10px;
    }

    footer p.content::before {
        left: 50%;
        transform: translateX(-50%);
        opacity: 1;
    }

    footer .mb3{
        margin-bottom: 30px;
    }
}