

.HomePageSlider .slider {
    width: 100%;
    overflow: hidden;
    position: relative;
}

.HomePageSlider .slider .wrapper {
    transition: 0.5s all linear;
    will-change: transform;
    height: 700px;
}

.HomePageSlider .slider .arrows {
    position: absolute;
    top: 50%;
    background: none;
    height: 60px;
    border: 0;
    cursor: pointer;
    transition: ease 0.3s all;
    outline: none;
    display: none;
}

.HomePageSlider .slider .arrows.prev {
    left: 0;
}

.HomePageSlider .slider .arrows.prev:hover {
    opacity: 0.7;
    left: -10px;
}

.HomePageSlider .slider .arrows.next {
    right: 0;
}

.HomePageSlider .slider .arrows.next:hover {
    right: -10px;
    opacity: 0.7;
}


.HomePageSlider .slider .bottom_side {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    background: #ad9956;
    padding: 4px;
}

.HomePageSlider .slider .slider_description {
    transition: 0.5s all linear;
    will-change: transform;
}

.HomePageSlider .slider .slider_description > div {
    width: 100vw;
    height: 100%;
    float: left;
    font-size: 20px;
    margin-bottom: 0;
}

.HomePageSlider .slider .slider_description p{
    margin: 0;
    font-size: 10px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 600;
    text-align: right;
    margin-right: calc(100% - 70%);
}

.HomePageSlider .slider .dots-container {
    padding: 0;
    margin: 0;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: calc(100% - 30%);
    background: #ad9956;
    text-align: right;
}

.HomePageSlider .slider .dots-container li {
    display: inline-block;
    line-height: 0;
}

.HomePageSlider .slider .dots-container li.active button {
    color: #fff;
}

.HomePageSlider .slider .dots-container li button {
    color: #000;
    background-color: transparent;
    border: none;
}

.HomePageSlider .slider .dots-container li button:hover {
    text-decoration: none;
    opacity: 0.7;
    cursor: pointer;
}


.HomePageSlider .slider .each-slide {
    width: 100vw;
    height: 100%;
    float: left;
    text-align: center;
    background-size: cover;
    background-position: center center;
    background-color: transparent;
}

.HomePageSlider .slider .each-slide img{
    width: 100vw;
    height: 100%;
}

.HomePageSlider .leftSide {
    height: 100%;
    position: relative;
}


.HomePageSlider .sliderHeading {
    opacity: 0;
    transition: 0.3s;
    position: absolute;
    height: 100%;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: end;
}

.HomePageSlider .sliderHeading.active {
    opacity: 1;
}

.HomePageSlider .animationText {
    position: absolute;
    right: -90px;
    z-index: 2;
}

.HomePageSlider .animationText h1{
    font-size: 34px;
    text-transform: uppercase;
    margin: 0;
    line-height: 30px;
    font-weight: 400;
    letter-spacing: 1px;
}

.HomePageSlider .animationText h1.heading1{
    transform: translateX(-60px);
    opacity: 0;
    transition: 0.5s;
}

.HomePageSlider .sliderHeading.active .animationText h1.heading1{
    transform: translateX(0px);
    transition-delay: 0.6s;
    opacity: 1;
}

.HomePageSlider .animationText h1.heading2 {
    padding-left: 40px;
    font-weight: 700;
    transform: translateY(40px);
    opacity: 0;
    transition: 0.5s;
}

.HomePageSlider .sliderHeading.active .animationText h1.heading2{
    transform: translateY(0px);
    transition-delay: 0.75s;
    opacity: 1;
}

.HomePageSlider .animationText h1.heading3{
    padding-left: 74px;
    transform: translateX(60px);
    opacity: 0;
    transition: 0.5s;
}

.HomePageSlider .sliderHeading.active .animationText h1.heading3{
    transform: translateX(0px);
    transition-delay: 0.6s;
    opacity: 1;
}

.HomePageSlider .animationText .divider {
    position: absolute;
    height: 1.4px;
    width: 150px;
    background: #000;
    top: 50px;
    left: 34px;
    transform: rotate(-40deg) translateX(150px);
    opacity: 0;
    transition: 0.5s;
}

.HomePageSlider .sliderHeading.active .animationText .divider{
    transform: rotate(-40deg) translateX(0px);
    transition-delay: 0.7s;
    opacity: 1;
}

.HomePageSlider .leftSide .line{
    height: 80px;
    width: 1px;
    background: #000;
    position: absolute;
    bottom: 49px;
    left: 129px;

    transform: rotateX(90deg);
    opacity: 0;
    transition: 0.5s;
}

.HomePageSlider .leftSide .sliderHeading.active .line{
    transform: rotateX(0deg);
    transition-delay: 0.6s;
    opacity: 1;
}

@media only screen and (max-width: 768px) {

    .HomePageSlider{
        height: 400px;
    }

    .HomePageSlider .leftSide {
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .HomePageSlider .animationText {
        right: auto;
        left: 50%;
        transform: translate(-50%, -50%);
        top: 25%;
    }

    .HomePageSlider .slider .wrapper{
        height: 400px;
    }   

    .HomePageSlider .slider .each-slide{
        position: relative;
    }

    .HomePageSlider .animationText h1{
        color: #fff;
    }

    .HomePageSlider .animationText .divider{
        background: #fff;
    }

    .HomePageSlider .slider .each-slide::before{
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        background: #00000038;
        left: 0;
        top: 0;
    }

    .HomePageSlider .slider .bottom_side{
        height: 24px;
    }

    .HomePageSlider .slider .dots-container{
        width: 100%;
        text-align: center;
    }

    .HomePageSlider .slider .slider_description {
        margin-top: -30px;
        margin-left: 30px;
    }

    .HomePageSlider .slider .slider_description p{
        color: #fff;
    }

    .HomePageSlider .leftSide .line{
        display: none;
    }
}